import React, { useState, useEffect } from 'react';
import { dbService } from "fbase";
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { limitTitle, onReloadClick } from 'Functions';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWonSign, faMapMarkerAlt, faBuilding, faLayerGroup, faHome, faStore, faSnowplow, faIndustry, faHouseUser, faPhoneSquareAlt, faObjectGroup } from "@fortawesome/free-solid-svg-icons";
import { Link } from 'react-router-dom/cjs/react-router-dom';
import Loading from 'components/Loading';
import NoResult from 'components/NoResult';
import Navigation from 'components/Navigation';
import FooterLink from 'components/FooterLink';
import Footer from 'components/Footer';
import soldPic from '../images/sold.png';
import 'routes/Search.css';

const Search = ( props ) => {
    const [products, setProducts] = useState([]);
    const [isLoaded, setIsLoaded] = useState(false);
    const [noResult, setNoResult] = useState(false);
    const [searchInput, setSearchInput] = useState("");
    // eslint-disable-next-line
    const history = useHistory();
    const data = props.history.location.state;
    window.onload = () => {
        setIsLoaded(true);
    };
    useEffect(() => {
        if (data && data.data) {
            dbService
                .collection('products')
                .where('tags', 'array-contains-any', [data.data])
                .onSnapshot((snapshot) => {
                    let productArray = snapshot.docs.map((doc) => ({
                    id: doc.id,
                    ...doc.data(),
                    }));
                    let size = snapshot.size;
                    if (size <= 0) {
                        setNoResult(true);
                    } else {
                        setNoResult(false);
                    }
                    setProducts(productArray);
                })
        } else if (data && data.group) {
            dbService
                .collection('products')
                .where("type", "==", data.group)
                .orderBy("createdAt", "desc")
                .onSnapshot((snapshot) => {
                    let productArray = snapshot.docs.map((doc) => ({
                        id: doc.id,
                        ...doc.data(),
                    }));
                    let size = snapshot.size;
                    if (size <= 0) {
                        setNoResult(true);
                    } else {
                        setNoResult(false);
                    }
                    setProducts(productArray);
                })
        } else {
            dbService
            .collection('products')
            .orderBy("createdAt", "desc")
            .onSnapshot((snapshot) => {
                let productArray = snapshot.docs.map((doc) => ({
                id: doc.id,
                ...doc.data(),
                }));
                let size = snapshot.size;
                if (size <= 0) {
                    setNoResult(true);
                } else {
                    setNoResult(false);
                }  
                    setProducts(productArray);
                })
        }
        setIsLoaded(true);
    }, [data]);
    const onClick = (event) => {
        const {
            target: { id },
        } = event;
        dbService
            .collection('products')
            .where("type", "==", id)
            .orderBy("createdAt", "desc")
            .onSnapshot((snapshot) => {
                let productArray = snapshot.docs.map((doc) => ({
                    id: doc.id,
                    ...doc.data(),
                }));
                if (productArray.length <= 0) {
                    setNoResult(true);
                } else {
                    setNoResult(false);
                }
                    setProducts(productArray);
                });
    };
    const onChange = (event) => {
        const {
            target: { value },
        } = event;
        setSearchInput(value);
    };
    const onSubmit = async (event) => {
        event.preventDefault();
        if (searchInput !== "" && searchInput !== " ") {
            await dbService
                .collection('products')
                .where('tags', 'array-contains-any', [searchInput])
                .onSnapshot((snapshot) => {
                    let productArray = snapshot.docs.map((doc) => ({
                        id: doc.id,
                        ...doc.data(),
                    }));
                    let size = snapshot.size;
                    if (size <= 0) {
                        setNoResult(true);
                    } else {
                        setNoResult(false);
                        setProducts(productArray);
                    }
                });
        }
        setSearchInput("");
        document.querySelector("#search").reset(); 
    };
    return (
        <>
        <Navigation />
        <div data-aos="fade-up" className="product-recom search-page container">
            <h3 onClick={onReloadClick} className="product-recom-title col-lg-12">ë¬¼ê±´ ê²€ìƒ‰</h3>
            <nav className="product-recom-nav col-lg-12">
                <span id="ì£¼íƒ" onClick={onClick}>ë‹¤ê°€êµ¬/ìƒê°€ì£¼íƒ</span>
                <span id="ìƒê°€ê±´ë¬¼" onClick={onClick}>ìƒê°€ê±´ë¬¼</span>
                <span id="í† ì§€" onClick={onClick}>í† ì§€</span>
                <span id="ê³µì¥/ì°½ê³ " onClick={onClick}>ê³µì¥/ì°½ê³ </span>
                <span id="ì „ì›ì£¼íƒ" onClick={onClick}>ì „ì›ì£¼íƒ</span>
                <span id="ì•„íŒŒíŠ¸" onClick={onClick}>ì•„íŒŒíŠ¸</span>
            </nav>
            <form id="search" onSubmit={onSubmit} className="input-group input-group-lg home-search search-search">
                <input
                onChange={onChange}    
                type="text"
                autoComplete="off"        
                className="form-control"
                placeholder="ì§€ì—­ëª…/ì§€í•˜ì² ì—­ì„ ì…ë ¥í•´ì£¼ì„¸ìš”."
                name="search"
                value={searchInput}   
                />
                <input
                type="submit"
                value="ê²€ìƒ‰"
                className="btn search-btn"
                />
            </form>
            {!isLoaded
                ? <Loading />
                : noResult    
                ? <NoResult />
                : <>
                <div className="search-container container row">
                    { products.map((product) =>
                    <>
                    <div data-aos="fade-up" key={product.id} className="product-recom-item col-lg-4 col-md-6">
                        <h4><span className="product-id">ë§¤ë¬¼ë²ˆí˜¸-{product.number}</span>{limitTitle(product.title)}</h4>
                        <span className="product-action">ì¶”ì²œ</span>
                        { product.sold === "ì™„ë£Œ" && <img src={soldPic} className="product-sold" alt="sold" /> }
                        <img src={product.attachmentUrl} alt="product-pic"/>
                        <div className="product-recom-list">
                            <p className="product-won"><FontAwesomeIcon icon={faWonSign} /> {product.price}</p>
                            <p className="product-location"><FontAwesomeIcon icon={faMapMarkerAlt} />{product.region}</p>
                            <div className="product-detail">
                                <p className="col-xs-12 row">
                                    <span className="col-xs-6">
                                        { product.type === "ì£¼íƒ"
                                        ? <FontAwesomeIcon icon={faHome} /> 
                                        : product.type === "ìƒê°€ê±´ë¬¼"
                                        ? <FontAwesomeIcon icon={faStore} /> 
                                        : product.type === "í† ì§€"
                                        ? <FontAwesomeIcon icon={faSnowplow} />    
                                        : product.type === "ê³µì¥/ì°½ê³ "
                                        ? <FontAwesomeIcon icon={faIndustry} />    
                                        : product.type === "ì „ì›ì£¼íƒ"
                                        ? <FontAwesomeIcon icon={faHouseUser} />    
                                        : product.type === "ì•„íŒŒíŠ¸"
                                        ? <FontAwesomeIcon icon={faBuilding} /> 
                                        : "Error"                                                                                
                                    } {product.type}</span>
                                    <span className="col-xs-6">
                                        {product.structure === "ë¬¸ì˜"
                                        ? <FontAwesomeIcon icon={faPhoneSquareAlt} />
                                        : <FontAwesomeIcon icon={faObjectGroup} />} {product.structure}
                                    </span>
                                </p>
                                <p className="col-xs-12">
                                    <FontAwesomeIcon icon={faLayerGroup} />
                                    <span> {product.size}</span>
                                </p> 
                                <Link
                                className="detail-more"        
                                to={{
                                pathname: `/search/${product.id}`,
                                state: {
                                    id: product.id,
                                    number: product.number,
                                    title: product.title,
                                    content: product.content,
                                    price: product.price,
                                    region: product.region,
                                    type: product.type,
                                    size: product.size,
                                    structure: product.structure,
                                    sold: product.sold,
                                    attachmentUrl: product.attachmentUrl,
                                }
                                }}>ë”ë³´ê¸°
                                </Link>    
                            </div>     
                        </div>
                    </div>
                    </>
                    )} 
                </div>
            </>}
        </div> 
        <FooterLink/>
        <Footer/>
        </>
    );
};


export default Search;